<template>
  <div
    class="duration-300 fixed overflow-y-auto z-[1000] inset-0"
    :class="{ 'opacity-0 invisible': !isOpen, 'opacity-100 visible': isOpen }"
  >
    <div
      class="fixed top-0 left-0 w-full h-full bg-primary bg-opacity-50"
      @click="closeModal"
    ></div>
    <div
      class="relative flex justify-center items-center w-full min-h-[calc(100%-2.5rem)] max-w-[960px] pointer-events-none mx-auto my-5"
    >
      <div class="w-full pointer-events-auto">
        <h2 class="text-white bg-warning text-center py-5"></h2>
        <div class="bg-secondary">
          <div class="p-5">
            <h3 class="text-20px text-primary" v-if="navItem === 'product'">
              {{ delProduct.title }} ()
            </h3>
            <h3 class="text-20px text-primary" v-if="navItem === 'order'">
              {{ delOrder.id }} ()
            </h3>
            <h3 class="text-20px text-primary" v-if="navItem === 'coupon'">
              {{ delCoupon.title }} ()
            </h3>
            <h3 class="text-20px text-primary" v-if="navItem === 'article'">
              {{ delArticle.title }} ()
            </h3>
          </div>
          <div class="text-right border-primary border-t-2 p-5">
            <button
              type="button"
              class="duration-300 text-warning border-warning border-2 px-12 py-2.5 hover:text-white hover:bg-warning"
              @click="closeModal"
            >
              
            </button>
            <button
              type="button"
              class="duration-300 text-primary border-primary border-2 px-12 py-2.5 ml-2.5 hover:text-white hover:bg-primary"
              v-if="navItem === 'product'"
              @click="deleteProduct"
            >
              
            </button>
            <button
              type="button"
              class="duration-300 text-primary border-primary border-2 px-12 py-2.5 ml-2.5 hover:text-white hover:bg-primary"
              v-if="navItem === 'order'"
              @click="deleteOrder"
            >
              
            </button>
            <button
              type="button"
              class="duration-300 text-primary border-primary border-2 px-12 py-2.5 ml-2.5 hover:text-white hover:bg-primary"
              v-if="navItem === 'coupon'"
              @click="deleteCoupon"
            >
              
            </button>
            <button
              type="button"
              class="duration-300 text-primary border-primary border-2 px-12 py-2.5 ml-2.5 hover:text-white hover:bg-primary"
              v-if="navItem === 'article'"
              @click="deleteArticle"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Swal from 'sweetalert2';

export default {
  props: {
    delProduct: {
      type: Object,
      default() {
        return {};
      },
    },
    delOrder: {
      type: Object,
      default() {
        return {};
      },
    },
    delCoupon: {
      type: Object,
      default() {
        return {};
      },
    },
    delArticle: {
      type: Object,
      default() {
        return {};
      },
    },
    currentPage: {
      type: Number,
      default: 1,
    },
    navItem: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      isOpen: false,
    };
  },
  methods: {
    deleteProduct() {
      const status = '';
      const { id } = this.delProduct;
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/product/${id}`;
      this.$http
        .delete(url)
        .then(() => {
          Swal.fire({
            position: 'top-end',
            icon: 'success',
            title: status,
            showConfirmButton: false,
            timer: 1500,
          });
          this.$emit('update-product', this.currentPage);
          this.closeModal();
        })
        .catch((err) => {
          Swal.fire({
            position: 'top-end',
            icon: 'error',
            title: err.response.data.message,
            showConfirmButton: false,
            timer: 1500,
          });
        });
    },
    deleteOrder() {
      const status = '';
      const { id } = this.delOrder;
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/order/${id}`;
      this.$http
        .delete(url)
        .then(() => {
          Swal.fire({
            position: 'top-end',
            icon: 'success',
            title: status,
            showConfirmButton: false,
            timer: 1500,
          });
          this.$emit('update-order', this.currentPage);
          this.closeModal();
        })
        .catch((err) => {
          Swal.fire({
            position: 'top-end',
            icon: 'error',
            title: err.response.data.message,
            showConfirmButton: false,
            timer: 1500,
          });
        });
    },
    deleteCoupon() {
      const status = '';
      const { id } = this.delCoupon;
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/coupon/${id}`;
      this.$http
        .delete(url)
        .then(() => {
          Swal.fire({
            position: 'top-end',
            icon: 'success',
            title: status,
            showConfirmButton: false,
            timer: 1500,
          });
          this.$emit('update-coupon', this.currentPage);
          this.closeModal();
        })
        .catch((err) => {
          Swal.fire({
            position: 'top-end',
            icon: 'error',
            title: err.response.data.message,
            showConfirmButton: false,
            timer: 1500,
          });
        });
    },
    deleteArticle() {
      const status = '';
      const { id } = this.delArticle;
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/article/${id}`;
      this.$http
        .delete(url)
        .then(() => {
          Swal.fire({
            position: 'top-end',
            icon: 'success',
            title: status,
            showConfirmButton: false,
            timer: 1500,
          });
          this.$emit('update-article', this.currentPage);
          this.closeModal();
        })
        .catch((err) => {
          Swal.fire({
            position: 'top-end',
            icon: 'error',
            title: err.response.data.message,
            showConfirmButton: false,
            timer: 1500,
          });
        });
    },
    openModal() {
      this.isOpen = true;
    },
    closeModal() {
      this.isOpen = false;
    },
  },
};
</script>
